
<template>
    <div class="settings-modal" :class="{ 'settings-modal-open': isOpen }">
            <div class="global-settings-buttons">
                <p class="text-center title">{{ title }}</p>
                <a @click="close" class="btn-close" ><i class="icon icon-delete"></i></a>
            </div>
            <hr>
            <div class="px-4 global-settings__fields">
               <slot></slot>
            </div>
            <div @click="save" class="save-settings">Сохранить</div>
        </div>
</template>
<script>
export default {
    props: {
        title: {
            type: String,
            default: '',
        },
        isOpen: {
            type: Boolean,
            default: true
        }
    },
    data(){
        return {

        }
    },
    methods: {
        close(){
            this.$emit('close')
        },
        save(){
            this.$emit('save')
        }
    }
}
</script>

<style>
.settings-modal{
    position: fixed;
    right: -350px;
    top: 0;
    height: 100vh;
    width: 300px;
    z-index: 9999;
    background-color: #fff;
    color: #151515;
    box-shadow: 0 0 10px 0 #888;
    transition: 0.2s ease;
}
.settings-modal-open{
    right: 0;
}
.save-settings {
    position: fixed;
    bottom: 0px;
    padding: 20px;
    text-align: center;
    width: 300px;
    background: #26cae5;
    color: white;
    font-size: 22px;
    cursor: pointer;
}
</style>



