
<template>
    <div class="settings-panel" id="settings-panel">
        <div class="setting-item">
            <p class="btn btn-setting-item global-btn" data-toggle="tooltip" data-placement="bottom" @click="openGlobalStyle" title="Глобальные стили"><i class="icon icon-globe"></i></p>
            <p class="btn btn-setting-item settings-btn" data-toggle="tooltip" data-placement="bottom" @click="openSettings" title="Настройки"><i class="icon icon-settings-2"></i></p>
            <p class="btn btn-setting-item btn-setting-save" @click="save" data-toggle="tooltip" data-placement="bottom" title="Сохранить"><iconSave  /></p>
        </div>
        <settingModal :isOpen="openSetting" :title="'Настройки'" @close="()=>{this.openSetting = false}">
            <div class="form-group">
                <label>Instagram</label>
                <input class="form-control" v-model="linkInstagram" placeholder="Ссылка на Instagram" />
            </div>
            <div class="form-group">
                <label>Whatsapp</label>
                <input  class="form-control" v-model="phoneWhatsapp" placeholder="Телефон" id="WhatsappInput" />
            </div>
            <div class="form-group">
                <label>Адресс</label>
                <input v-model="geoLocal" class="form-control" placeholder="Адресс" />
                <div class="list-items-geo" v-if="geoLocalItems.length > 0">
                    <div class="list-item-geo" v-for="item in geoLocalItems" @click="setGeoItem(item)" >{{ item.displayName }}</div> 
                </div>
            </div>
        </settingModal>
        <settingModal :isOpen="openText" :title="'Стили текста'" @close="()=>{
                this.openText = false
                this.activeElem.changeStyle({
                    outline: '0px'
                })
                this.activeElem = null;
            }">
            <div class="form-group">
                <label for="font-select">Шрифт</label>
                <select name="font-family" v-model="textFontFamily" class="form-control" id="font-family">
                    <option value="Arial">Arial</option>
                    <option value="Roboto">Roboto</option>
                    <option value="Montserrat">Montserrat</option>
                    <option value="Rubik">Rubik</option>
                </select>
            </div>
            <div class="form-group">
                <label for="font-select">Размер</label>
                <select v-model="textFontSize" name="font-size" class="form-control" id="font-size">
                    <option value="10px">10px</option>
                    <option value="12px">12px</option>
                    <option value="14px">14px</option>
                    <option value="16px">16px</option>
                    <option value="20px">20px</option>
                    <option value="24px">24px</option>
                    <option value="28px">28px</option>
                    <option value="32px">32px</option>
                    <option value="36px">36px</option>
                    <option value="40px">40px</option>
                </select>
            </div>
            <div class="form-group">
                <label for="font-select">Жирность</label>
                <select v-model="textFontWeight" name="font-weight" class="form-control" id="font-weight">
                    <option value="100">100, Thin</option>
                    <option value="300">300, Light</option>
                    <option value="400">400, Normal</option>
                    <option value="500">500, Medium</option>
                    <option value="600">600, Semi Bold</option>
                    <option value="700">700, Bold</option>
                </select>
            </div>
            <div class="form-group">
                <label for="font-select">Цвет</label>
                <colorPicker @change="changeColorText" :color="colorText" />
            </div>
        </settingModal>
        <settingModal :isOpen="openGlobal" :title="'Шлобальные стили'" @close="()=>{this.openGlobal = false}">
            <div class="form-group">
                <label for="font-select">Шрифт</label>
                <select class="form-control" id="font-select">
                    <option value="Arial">Arial</option>
                    <option value="Roboto">Roboto</option>
                    <option value="Montserrat">Montserrat</option>
                    <option value="Times New Romans">Times New Romans</option>
                </select>
            </div>
            <div class="form-group">
                <label for="font-select">Общий цвет</label>
                <div class="color-options">
                    <div v-for="color in defaultColorsGlobal" class="color-options__item" @click="changeColorGlobal(color)" :style="{ 'background': color.hex }" ></div>
                </div>
                <colorPicker @change="changeColorGlobal" :color="colorGlobal" />
            </div>
            <div class="form-group">
                <label for="font-select">Цвет при наведение</label>
                <div class="color-options">
                    <div v-for="color in defaultColorsHover" class="color-options__item" @click="changeColorHover(color)" :style="{ 'background': color.hex }" ></div>
                </div>
                <colorPicker @change="changeColorHover" :color="colorHover" />
            </div>
            <div class="form-group" id="background-color">
                <label for="font-select">Цвет фона</label>
                <div class="color-options">
                    <div v-for="color in defaultColorsBackground" class="color-options__item" @click="changeColorBackground(color)" :style="{ 'background': color.hex }" ></div>
                </div>
                <colorPicker @change="changeColorBackground" :color="colorBackground" />
            </div>
            <div class="form-group">
                <label for="font-select">Логотип</label>
                <div class="file-drop-area">
                    <span class="fake-btn">Выберите файл <br>Или перетащите его сюда </span>
                    <input v-on:change="changeLogo" ref="fileLogo" class="file-input" type="file">
                </div>
            </div>
        </settingModal>
        <settingModal :isOpen="openBannerSetting" :title="'Настройка баннера'" @close="saveBanner" @save="saveBanner">
            <div id="file-input" class="form-group">
                <label for="font-select">Изображение баннера</label>
                <div class="file-drop-area" >
                    <span class="fake-btn">Выберите файл <br>Или перетащите его сюда </span>
                    <input v-on:change="changeBanner" ref="fileBanner" class="file-input" type="file">
                </div>
            </div>
        </settingModal>
        <div class="switch-panel open">
            <div>
                <a href="#" class="btn btn-primary btn-block active">Главная</a>
                <a href="#" class="btn btn-primary btn-block">Каталог</a>
                <a href="file:///C:/Users/shepi/OneDrive/%D0%A0%D0%B0%D0%B1%D0%BE%D1%87%D0%B8%D0%B9%20%D1%81%D1%82%D0%BE%D0%BB/git/templates-gosauda/template_main/item_shop.html" class="btn btn-primary btn-block">Просмотр товара</a>
                <a href="#" class="btn btn-primary btn-block">О компании</a>
            </div>
    </div>
    </div>
</template>
<script>
import colorPicker from './components/color-picker';
import axios from 'axios';
import iconSave from './icons/save'
import settingModal from './components/setting-modal';
export default {
    components: {
        colorPicker,
        iconSave,
        settingModal
    },
    data() {
        return{
            linkInstagram: '',
            phoneWhatsapp: '',
            instaTokens:{
                I: {
                    pattern: '/^https://(www\.)?instagram\.com/g',
                },
                I2: {
                    pattern: /[0-9a-fA-F]/,
                    transform: v => 'n/'+ v
                },
                T: {
                    pattern: /[0-9a-fA-F]+/
                }
            },
            defaultColorsGlobal: [
                { hex: '#26cae5' },
                { hex: '#87b476' },
                { hex: '#b47690' },
                { hex: '#767cb4' }
            ],
            colorGlobal: { hex: '#26cae5' },
            defaultColorsHover: [
                { hex: '#26cae5' },
                { hex: '#87b476' },
                { hex: '#b47690' },
                { hex: '#767cb4' }
            ],
            colorHover: { hex: '#26cae5' },
            defaultColorsBackground: [
                { hex: '#26cae5' },
                { hex: '#87b476' },
                { hex: '#b47690' },
                { hex: '#767cb4' }
            ],
            colorBackground: { hex: '#f2f2f2' },

            activeElem: null,
            openText: false,
            openGlobal: false,
            openSetting: false,
            openBannerSetting: false,

            textBanner: '',
            textBannerFontFamily: '',
            textBannerFontSize: '',
            textBannerFontWeight: '',
            colorTextBanner: {},
            positionBannerImg: 0,
            maxTopBannerImg: 0,

            textFontFamily: '',
            textFontSize: '',
            textFontWeight: '',
            colorText: {},

            geoLocal: '',
            geoLocalItems: [],
        }
    },
    created(){
        let data;
        let cntx = this;
        $('.banner-img>img').click(()=>{
            
            $('.banner-img').attr('style', '');
            $('.banner-img>img').addClass('cropper-hidden');
            this.openBannerSetting = true;
            if(this.cropper){
                $('.cropper-container').css('display', 'block');
                return;
            }
                
            this.cropper = new Cropper($('.banner-img>img')[0], {
                viewMode: 3,
                dragMode: 'move',
                autoCropArea: 1,
                restore: false,
                modal: false,
                guides: false,
                highlight: false,
                cropBoxMovable: false,
                cropBoxResizable: false,
                toggleDragModeOnDblclick: false,
                crop(event) {
                    data = this.cropper.getData()
                    console.log(data);
                    cntx.stylePositionBannerWrapper = $('.cropper-canvas').attr('style');
                    cntx.stylePositionBannerImg = $('.cropper-canvas img').attr('style');
                }
            });            
        })
        let heigth = $('.banner-img img').css('heigth');
        this.maxTopBannerImg = heigth - 300;
        this.textBanner = $('.banner-title').text();
        
    },
    watch: {
        positionBannerImg(){
            $('.banner-img img').css('top', -this.positionBannerImg);
        },
        textBanner(){
            $('.banner-title').text(this.textBanner);
        },
        linkInstagram(){
            $('#instagram-link').attr('href', 'https://www.instagram.com/' + this.linkInstagram);
        },
        phoneWhatsapp(){
            $('#whatsapp-link').attr('href', 'https://wa.me/' + this.phoneWhatsapp);
        },
        geoLocal(){
            ymaps.suggest('Казахстан, ' + this.geoLocal).then((items) => {
                this.geoLocalItems = items;
            });
        },
        textFontFamily(){
            this.activeElem.changeStyle({
                'font-family': this.textFontFamily
            })
        },
        textFontSize(){
            this.activeElem.changeStyle({
                'font-size': this.textFontSize
            })
        },
        textFontWeight(){
            this.activeElem.changeStyle({
                'font-weight': this.textFontWeight
            })
        },
        textBannerFontFamily(){
            $('.banner-title').css({
                'font-family': this.textBannerFontFamily
            })
        },
        textBannerFontSize(){
            $('.banner-title').css({
                'font-size': this.textBannerFontSize
            })
        },
        textBannerFontWeight(){
            $('.banner-title').css({
                'font-weight': this.textBannerFontWeight
            })
        },
        positionImgBanner(){
            $('.')
        },
        activeElem(){
            console.log(this.activeElem);
            if(this.activeElem != null){
                this.textFontFamily = this.activeElem.$data.style['font-family'];
                this.textFontSize = this.activeElem.$data.style['font-size'];
                this.textFontWeight = this.activeElem.$data.style['font-weight'];
                this.colorText = { hex: this.activeElem.$data.style['color'] };
                this.openText = true;
                this.openGlobal = false;
                this.openSetting = false;
                this.openBannerSetting = false;
            }
            else{
                this.openText = false;
            } 
        },
        colorGlobal(){
            $('.icon-setting, .card-price, .link').css('color', this.colorGlobal.hex);
            $('.border-setting').css('border-color', this.colorGlobal.hex);
            $('.background-setting').css('background', this.colorGlobal.hex);
        },
        colorHover(){
            $('a').attr('onmouseover', '$(this).css("color", "' + this.colorHover.hex + '")' );
            $('a').attr('onmouseout', '$(this).css("color", "")' );

        },
        colorBackground(){
            $('.shop').css('background', this.colorBackground.hex);
        }
    },
    computed:{
    },
    methods: {
        saveBanner(){
            $('.banner-img').attr('style', this.stylePositionBannerWrapper);
            $('.cropper-container').css('display', 'none');
            $('.banner-img>img').removeClass('cropper-hidden');
            this.openBannerSetting = false;
        },
        setGeoItem(item){
            $('#adress').text(item.displayName);
            this.geoLocal = item.displayName;
            this.geoLocalItems = [];
        },
        changeColorTextBanner(color){
            this.colorTextBanner = color;
            $('.banner-title').css('color', color.hex);
        },
        changeColorText(color){
            this.colorText = color;
            this.activeElem.changeColor(color.hex);
        },
        closeTextSettings(){
            this.activeElem.changeStyle({
                outline: '0px'
            })
            console.log(this.activeElem = style);
            this.openText = false;
        },
        closeSettings(){
            this.openSetting = false;
        },
        closeGlobalSettings(){
            this.openGlobal = false;
        },
        changeColorGlobal(color){
            this.colorGlobal = color;
        },
        changeColorHover(color){
            this.colorHover = color;
        },
        changeColorBackground(color){
            this.colorBackground = color;
        },
        openGlobalStyle(){
            this.activeElem = null;
            if(this.openGlobal){
                this.openGlobal = false;
            }else{
                this.openGlobal = true;
                this.openSetting = false;
                this.openText = false;
                this.openBannerSetting = false;
            }
            
        },
        openSettings(){
            this.activeElem = null;
            if(this.openSetting){
                this.openSetting = false;
            }else{
                this.openSetting = true;
                this.openText = false;
                this.openGlobal = false;
                this.openBannerSetting = false;
            }
            
        },
        changeBanner(){
            let file = this.$refs.fileBanner.files;
            if (!file.length)
                return;
            this.createImage(file[0], (e) => {
                $('.banner-img img').attr('src', e.target.result )
            });
        },
        changeLogo(){
            let file = this.$refs.fileLogo.files;
            if (!file.length)
                return;
            this.createImage(file[0], (e) => {
                $('.logo').attr('src', e.target.result )
            });
        },
        createImage: function(file, callback) {
            let reader = new FileReader();
            reader.onload = callback;
            reader.readAsDataURL(file);
        },
        save(){
            axios.post('/shop/tpl_save', {
                params: {
                    html: $('html').html()
                }
            })
        }
    }
}
</script>

<style>
.list-items-geo {
    border: 1px solid #ccc;
    margin: auto;
    border-top: 0px;
    margin-top: -12px;
    padding-top: 12px;
}
.list-item-geo {
    margin: auto;
    border: 1px solid #ccc;
    background: #f4f4f4;
    padding: 5px 15px;
    color: black;
    margin-bottom: 10px;
    margin-top: 10px;
    border-radius: 10px;
    cursor: pointer;
    width: 95%;
    font-size: 14px;
}
.btn-setting-save svg{
    width: 18px;
}
.btn-setting-save:hover svg{
    fill: white;
}
.banner:hover{
    border-color: #26cae5;
    transition: all 300ms;
}
.slider-range-container {
  width: 100%; /* Width of the outside container */
}


.slider-range {
  -webkit-appearance: none;
  width: 100%;
  height: 15px;
  border-radius: 5px;  
  background: #d3d3d3;
  outline: none;
  opacity: 0.7;
  -webkit-transition: .2s;
  transition: opacity .2s;
}

.slider-range::-webkit-slider-thumb {
  -webkit-appearance: none;
  appearance: none;
  width: 25px;
  height: 25px;
  border-radius: 50%; 
  background: #26cae5;
  cursor: pointer;
}

.slider-range::-moz-range-thumb {
  width: 25px;
  height: 25px;
  border-radius: 50%;
  background: #26cae5;
  cursor: pointer;
}
</style>



